import { useState } from "react";

export default function TrackOrder() {
  const [orderId, setOrderId] = useState("");
  interface OrderDetails {
    orderId: string;
    status: string;
    customerName: string;
    deliveryDate?: string;
  }

  const [orderDetails, setOrderDetails] = useState<OrderDetails | null>(null);
  const [error, setError] = useState("");

  const handleSearch = async () => {
    setError("");
    setOrderDetails(null);

    try {
      const response = await fetch(`/api/logistics?orderId=${orderId}`);
      if (response.ok) {
        const data = await response.json();
        setOrderDetails(data);
      } else {
        const err = await response.json();
        setError(err.error || "Something went wrong");
      }
    } catch (err) {
      setError("Failed to fetch order details. Please try again later.");
    }
  };

  return (
    <div style={{ padding: "20px" }}>
      <h1>Track Your Order</h1>
      <input type="text" placeholder="Enter Order ID" value={orderId} onChange={(e) => setOrderId(e.target.value)} style={{ marginRight: "10px" }}/>
      <button onClick={handleSearch}>Track</button>
      {error && <p style={{ color: "red" }}>{error}</p>}
      {orderDetails && (
        <div>
          <h2>Order Details</h2>
          <p><strong>Order ID:</strong> {orderDetails.orderId}</p>
          <p><strong>Status:</strong> {orderDetails.status}</p>
          <p><strong>Customer Name:</strong> {orderDetails.customerName}</p>
          <p><strong>Delivery Date:</strong> {orderDetails.deliveryDate || "N/A"}</p>
        </div>
      )}
    </div>
  );
}
